<template>
	<div class="service_contents service-first"><!-- 액자 -->
		<select class="selectBold sam2" @change="onChange($event)" v-model="enterRoomIndex">
			<option value="">입관실 선택</option>
			<option v-for="(enterRoom,index) in getDisplayEnterRoomList" :key="index" :value="index">{{enterRoom.enterRoomNumber}}</option>
		</select>
		<br>
		<select class="selectBold sam2" v-model="displayState.displayStateDisplay.id">
			<option value="">전광판 선택</option>
			<option v-for="(display,index) in getDisplayList" :key="index" :value="display.id">{{display.id}}{{displayMortuaryTxt(display.displayDisplayState)}}</option>
		</select>
		<div class="btn_area_inner"><button class="save_btn"  @click="displayStateSave">저장하기</button></div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
  name: 'Display',
	mixins: [myMixin],
  components: { 
	},
  	props: {
  	},
    data(){
        return{
			displayState:{
				id:null,
				displayStateType:{
					codeId:null
				},
				displayStateDisplay:{
					id:''
				},
				displayStateMortuary:{
					id:null
				},
				displayStateAgeYn:true,
				displayStateSexYn:true,
				displayStatePictureYn:true,
				displayStateSonYn:true,
				displayStateSonWifeYn:true,
				displayStateDaughterYn:true,
				displayStateDaughterWifeYn:true,
				displayStateGrandSonYn:true,
				displayStateOneGrandSonYn:true,
				displayStateEnterYn:true,
				displayStateEndYn:true,
				displayStatePlaceYn:true,
				displayStateEtc:true,
				displayStateDisplayManagerState:null
			},
			enterRoomIndex: '',
			displayType : 'DISPLAY-3'

		}
	},
	computed:{
		...mapGetters(['getDisplayList','getDisplayEnterRoomList']),
	},
   	mounted() {
	},
	methods:{ 
		onChange(event) {
			
			if(this.getDisplayEnterRoomList[event.target.value].enterRoomDisplayState!== null && this.getDisplayEnterRoomList[event.target.value].enterRoomDisplayState!== undefined){
                for(let i=0;i <this.getDisplayEnterRoomList[event.target.value].enterRoomDisplayState.length;i++){
                    if(this.getDisplayEnterRoomList[event.target.value].enterRoomDisplayState[i].displayStateType.codeId === this.displayType){
                        this.displayState = this.getDisplayEnterRoomList[event.target.value].enterRoomDisplayState[i]
                        return
                    }
                }

			}
			this.displayState ={
								id:null,
								displayStateType:{
									codeId:null
								},
								displayStateDisplay:{
									id:''
								},
								displayStateMortuary:{
									id:null
								},
								displayStateAgeYn:true,
								displayStateSexYn:true,
								displayStatePictureYn:true,
								displayStateSonYn:true,
								displayStateSonWifeYn:true,
								displayStateDaughterYn:true,
								displayStateDaughterWifeYn:true,
								displayStateGrandSonYn:true,
								displayStateOneGrandSonYn:true,
								displayStateEnterYn:true,
								displayStateEndYn:true,
								displayStatePlaceYn:true,
								displayStateEtc:true,
								displayStateDisplayManagerState:null
							}
		},
		displayMortuaryTxt(type){
			
			if(type!==null && type !== undefined){
				if(type.displayStateType!==null && type.displayStateType !== undefined){
					if( type.displayStateType.codeId === 'DISPLAY-1'){
                        if(type.displayStateMortuary !==null && type.displayStateMortuary.mortuaryNumber!==undefined){
							return '(' + type.displayStateType.codeName + type.displayStateMortuary.mortuaryNumber + '로 이용중)'
						}else{
							return ''
						}
					}else{

						return '(' + type.displayStateType.codeName + '로 이용중)'
					}
				}
			}
			return ''
		},
		displayStateSave(){
			
			
			this.displayState.displayStateMortuary = null
			this.displayState.displayStateEnterRoom = {
				id:this.getDisplayEnterRoomList[this.enterRoomIndex].id 
				}
			this.displayState.displayStateType = {
					codeId:this.displayType
				}
			const parameter = {
				displayState:this.displayState
			}
			
			this.$store.dispatch('REQUEST_REGIST_MORTUARY_DISPLAYSTATE',parameter)
		}
	}
}
</script>

