<template>
	<div class="service_contents service-first">
		<!-- 분향소알림판 -->
		<select class="selectBold sam2" @change="onChange($event)" v-model="mortuaryIndex">
				<option value="">빈소선택</option>
				<option v-for="(mortuarty,index) in getDisplayMortuaryList" :key="index" :value="index">{{mortuarty.mortuaryNumber}}</option>
			</select>
		<p class="smallTitle">전광판</p>
		<select class="selectNormal sam2" v-model="displayState.displayStateDisplay.id">
				<option value="">전광판 선택</option>
				<option v-for="(display,index) in getDisplayList" :key="index" :value="display.id">{{display.id}}{{displayMortuaryTxt(display.displayDisplayState)}}</option>
			</select>
		<ul class="seviceUl">
			<li>고인</li>
			<li><input type="checkbox" v-model="displayState.displayStateAgeYn"><span class="chkStyle">나이 노출</span> <input type="checkbox" v-model="displayState.displayStateSexYn">성별 노출</li>
			<li>사진</li>
			<li><input type="checkbox" v-model="displayState.displayStatePictureYn"><span class="chkStyle">표시함</span></li>
			<li>상주</li>
			<li><input type="checkbox" v-model="displayState.displayStateSonYn"><span class="chkStyle">아들</span> <input type="checkbox" v-model="displayState.displayStateSonWifeYn"><span class="chkStyle">자부</span>
				<input type="checkbox" v-model="displayState.displayStateDaughterYn"><span class="chkStyle">딸</span> <input type="checkbox" v-model="displayState.displayStateDaughterWifeYn"><span class="chkStyle">사위</span>
				<input type="checkbox" v-model="displayState.displayStateGrandSonYn"><span class="chkStyle">손</span><br><input type="checkbox" v-model="displayState.displayStateOneGrandSonYn">외촌</li>
			<li>장례</li>
			<li>
				<input type="checkbox" v-model="displayState.displayStateEnterYn"><span class="chkStyle">입관 미노출</span>
				<input type="checkbox" v-model="displayState.displayStateEndYn"><span class="chkStyle">발인 미노출</span>
				<input type="checkbox" v-model="displayState.displayStatePlaceYn">장지 미노출
			</li>
			<li>모니터</li>
			<li><input type="checkbox" :checked="!displayState.displayStateVerticalYn" @click="displayState.displayStateVerticalYn = false"><span class="chkStyle">가로형</span><input type="checkbox" :checked="displayState.displayStateVerticalYn"  @click="displayState.displayStateVerticalYn = true">세로형</li>
		</ul>
		<div class="btn_area_inner"><!--<button class="preview_btn">미리보기</button>--><button class="save_btn" @click="displayStateSave">저장하기</button></div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
	name: 'Display',
	mixins: [myMixin],
	components: {},
	props: {},
	data() {
		return {
			displayState: {
				id: null,
				displayStateType: {
					codeId: null
				},
				displayStateDisplay: {
					id: ''
				},
				displayStateMortuary: {
					id: null
				},
				displayStateAgeYn: true,
				displayStateSexYn: true,
				displayStatePictureYn: true,
				displayStateSonYn: true,
				displayStateSonWifeYn: true,
				displayStateDaughterYn: true,
				displayStateDaughterWifeYn: true,
				displayStateGrandSonYn: true,
				displayStateOneGrandSonYn: true,
				displayStateEnterYn: true,
				displayStateEndYn: true,
				displayStatePlaceYn: true,
				displayStateEtc: true,
				displayStateVerticalYn:false,
				displayStateDisplayManagerState: null
			},
			mortuaryIndex: '',
			displayType: 'DISPLAY-1'
		}
	},
	computed: {
		...mapGetters(['getDisplayList', 'getDisplayMortuaryList']),
	},
	methods: {
		displayMortuaryTxt(type) {
			if (type !== null && type !== undefined) {
				if (type.displayStateType !== null && type.displayStateType !== undefined) {
					if (type.displayStateType.codeId === 'DISPLAY-1') {
						if (type.displayStateMortuary !== null && type.displayStateMortuary.mortuaryNumber !== undefined) {
							return '(' + type.displayStateType.codeName + type.displayStateMortuary.mortuaryNumber + '로 이용중)'
						} else {
							return ''
						}
					} else {
						return '(' + type.displayStateType.codeName + '로 이용중)'
					}
				}
			}
			return ''
		},
		onChange(event) {
			if (this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState !== null && this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState !== undefined) {
				for (let i = 0; i < this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState.length; i++) {
					if (this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState[i].displayStateType.codeId === this.displayType) {
						this.displayState = this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState[i]
						return
					}
				}
			}
			this.displayState = {
				id: null,
				displayStateType: {
					codeId: null
				},
				displayStateDisplay: {
					id: ''
				},
				displayStateMortuary: {
					id: null
				},
				displayStateAgeYn: true,
				displayStateSexYn: true,
				displayStatePictureYn: true,
				displayStateSonYn: true,
				displayStateSonWifeYn: true,
				displayStateDaughterYn: true,
				displayStateDaughterWifeYn: true,
				displayStateGrandSonYn: true,
				displayStateOneGrandSonYn: true,
				displayStateEnterYn: true,
				displayStateEndYn: true,
				displayStatePlaceYn: true,
				displayStateEtc: true,
				displayStateVerticalYn:false,
				displayStateDisplayManagerState: null
			}
		},
		displayStateSave() {
			this.displayState.displayStateMortuary = {
				id: this.getDisplayMortuaryList[this.mortuaryIndex].id
			}
			this.displayState.displayStateType = {
				codeId: this.displayType
			}
			const parameter = {
				displayState: this.displayState
			}
			this.$store.dispatch('REQUEST_REGIST_MORTUARY_DISPLAYSTATE', parameter)
		}
	}
}
</script>

