<template>
	<div class="service_contents picture  service-first">
		<!-- 디지털영정사진 -->
		<select class="selectNormal topMargin30 sam2" @change="onChange($event)" v-model="mortuaryIndex">
				<option value="">빈소선택</option>
				<option v-for="(mortuarty,index) in getDisplayMortuaryList" :key="index" :value="index">{{mortuarty.mortuaryNumber}}</option>
		</select>
		<select class="selectNormal topMargin30 sam2" v-model="displayState.displayStateDisplay.id">
				<option value="">전광판 선택</option>
				<option v-for="(display,index) in getDisplayList" :key="index" :value="display.id">{{display.id}}{{displayMortuaryTxt(display.displayDisplayState)}}</option>
		</select>

		<br>
		<br>
		
		<div style="display:inline">
		<input type="checkbox" :checked="!displayState.displayStateVerticalYn" @click="displayState.displayStateVerticalYn = false"><span class="chkStyle">가로형</span>
		<input type="checkbox"  :checked="displayState.displayStateVerticalYn"  @click="displayState.displayStateVerticalYn = true">세로형
		</div>

		<div class="btn_area_inner"><button class="save_big_btn" @click="displayStateSave">저장하기</button></div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
	name: 'Display',
	mixins: [myMixin],
	components: {},
	props: {},
	data() {
		return {
			displayState: {
				id: null,
				displayStateType: {
					codeId: null
				},
				displayStateDisplay: {
					id: ''
				},
				displayStateMortuary: {
					id: null
				},
				displayStateAgeYn: true,
				displayStateSexYn: true,
				displayStatePictureYn: true,
				displayStateSonYn: true,
				displayStateSonWifeYn: true,
				displayStateDaughterYn: true,
				displayStateDaughterWifeYn: true,
				displayStateGrandSonYn: true,
				displayStateOneGrandSonYn: true,
				displayStateEnterYn: true,
				displayStateEndYn: true,
				displayStatePlaceYn: true,
				displayStateEtc: true,
				displayStateDisplayManagerState: null
			},
			mortuaryIndex: '',
			displayType: 'DISPLAY-2'
		}
	},
	computed: {
		...mapGetters(['getDisplayList', 'getDisplayMortuaryList']),
	},
	methods: {
		onChange(event) {
			if (this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState !== null && this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState !== undefined) {
				for (let i = 0; i < this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState.length; i++) {
					if (this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState[i].displayStateType.codeId === this.displayType) {
						this.displayState = this.getDisplayMortuaryList[event.target.value].mortuaryDisplayState[i]
						return
					}
				}
			}
			this.displayState = {
				id: null,
				displayStateType: {
					codeId: null
				},
				displayStateDisplay: {
					id: ''
				},
				displayStateMortuary: {
					id: null
				},
				displayStateAgeYn: true,
				displayStateSexYn: true,
				displayStatePictureYn: true,
				displayStateSonYn: true,
				displayStateSonWifeYn: true,
				displayStateDaughterYn: true,
				displayStateDaughterWifeYn: true,
				displayStateGrandSonYn: true,
				displayStateOneGrandSonYn: true,
				displayStateEnterYn: true,
				displayStateEndYn: true,
				displayStatePlaceYn: true,
				displayStateEtc: true,
				displayStateDisplayManagerState: null
			}
		},
		displayMortuaryTxt(type) {
			if (type !== null && type !== undefined) {
				if (type.displayStateType !== null && type.displayStateType !== undefined) {
					if (type.displayStateType.codeId === 'DISPLAY-1') {
						if (type.displayStateMortuary !== null && type.displayStateMortuary.mortuaryNumber !== undefined) {
							return '(' + type.displayStateType.codeName + type.displayStateMortuary.mortuaryNumber + '로 이용중)'
						} else {
							return ''
						}
					} else {
						return '(' + type.displayStateType.codeName + '로 이용중)'
					}
				}
			}
			return ''
		},
		displayStateSave() {
			this.displayState.displayStateMortuary = {
				id: this.getDisplayMortuaryList[this.mortuaryIndex].id
			}
			this.displayState.displayStateType = {
				codeId: this.displayType
			}
			const parameter = {
				displayState: this.displayState
			}
			this.$store.dispatch('REQUEST_REGIST_MORTUARY_DISPLAYSTATE', parameter)
		}
	}
}
</script>

