<template>
	<div class="service_contents  service-first">
		<!-- 현황판 -->
		<select class="selectBold sam2" v-model="displayState.displayStateDisplay.id" @change="onChange($event)">
				<option value="">전광판 선택</option>
				<option v-for="(display,index) in getDisplayList" :key="index" :value="display.id">{{display.id}}{{displayMortuaryTxt(display.displayDisplayState)}}</option>
			</select>
		<ul class="seviceUl">
			<li style="width:20%;">모니터</li>
			<li style="width:80%;"><input type="checkbox"><span class="chkStyle">가로형</span><input type="checkbox">세로형</li>
		</ul>
		<p class="smallTitle">한 화면에 들어갈 빈소 갯수</p>
		<p class="smallTxt btmMargin10">※미리보기로 자동완성된 현황판을 확인하신 후<br>한 화면에 들어갈 적당한 빈소갯수를 미리보기로 확인한 뒤 갯수를 입력해주세요.</p>
		<input type="text" class="input02" v-model="displayState.displayStateLineSize"><!--<button class="preview_small_btn">미리보기</button>
		<button class="preview_small_btn">미리보기</button>-->
		<div class="btn_area_inner"><button class="save_big_btn" @click="displayStateSave">저장하기</button></div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
	name: 'Display',
	mixins: [myMixin],
	components: {},
	props: {},
	data() {
		return {
			displayState: {
				id: null,
				displayStateType: {
					codeId: null
				},
				displayStateDisplay: {
					id: ''
				},
				displayStateMortuary: {
					id: null
				},
				displayStateAgeYn: true,
				displayStateSexYn: true,
				displayStatePictureYn: true,
				displayStateSonYn: true,
				displayStateSonWifeYn: true,
				displayStateDaughterYn: true,
				displayStateDaughterWifeYn: true,
				displayStateGrandSonYn: true,
				displayStateOneGrandSonYn: true,
				displayStateEnterYn: true,
				displayStateEndYn: true,
				displayStatePlaceYn: true,
				displayStateEtc: true,
				displayStateDisplayManagerState: null,
				displayStateLineSize: 7
			},
			mortuaryIndex: '',
			displayType: 'DISPLAY-0'
		}
	},
	computed: {
		...mapGetters(['getDisplayList', 'getDisplayMortuaryList']),
	},
	methods: {
		displayMortuaryTxt(type) {
			if (type !== null && type !== undefined) {
				if (type.displayStateType !== null && type.displayStateType !== undefined) {
					if (type.displayStateType.codeId === 'DISPLAY-1') {
						if (type.displayStateMortuary !== null && type.displayStateMortuary.mortuaryNumber !== undefined) {
							return '(' + type.displayStateType.codeName + type.displayStateMortuary.mortuaryNumber + '로 이용중)'
						} else {
							return ''
						}
					} else {
						return '(' + type.displayStateType.codeName + '로 이용중)'
					}
				}
			}
			return ''
		},
		onChange(event) {
			if (event.target.value !== null && event.target.value !== '') {
				for (let i = 0; i < this.getDisplayList.length; i++) {
					if (this.getDisplayList[i].id === event.target.value) {
						this.displayState = this.getDisplayList[i].displayDisplayState
						this.displayState.displayStateDisplay = { id: this.getDisplayList[i].id }
						return
					}
				}
			}
			this.displayState = {
				id: null,
				displayStateType: {
					codeId: null
				},
				displayStateDisplay: {
					id: ''
				},
				displayStateMortuary: {
					id: null
				},
				displayStateAgeYn: true,
				displayStateSexYn: true,
				displayStatePictureYn: true,
				displayStateSonYn: true,
				displayStateSonWifeYn: true,
				displayStateDaughterYn: true,
				displayStateDaughterWifeYn: true,
				displayStateGrandSonYn: true,
				displayStateOneGrandSonYn: true,
				displayStateEnterYn: true,
				displayStateEndYn: true,
				displayStatePlaceYn: true,
				displayStateEtc: true,
				displayStateDisplayManagerState: null,
				displayStateLineSize: 7
			}
		},
		displayStateSave() {
			this.displayState.displayStateMortuary = null
			this.displayState.displayStateType = {
				codeId: this.displayType
			}
			const parameter = {
				displayState: this.displayState
			}
			this.$store.dispatch('REQUEST_REGIST_MORTUARY_DISPLAYSTATE', parameter)
		}
	}
}
</script>

